*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: rgb(226, 226, 226);
    /* background: white; */
}
.cell{
    border: 5px solid blueviolet;
    border-radius: 20px;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items:  center;
    font-size: 80px;
}

.row{
    display: flex;
    justify-content: center;
    align-items: center ;
}
.gameOver{
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center ;
    font-size: 50px;
    color: yellow;

}
.wrapper{
    display: flex;
    justify-content: center;
    align-items: center ;
    color: blueviolet;

}